.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.align-center {
  text-align: center;
  /* font-family: arial, sans-serif; */
}

.large-tables {
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 100%;
}

.large-tables td, .large-tables th {
  /* border: 1px solid #ddd; */
  border: none;
  padding: 8px;
}

.large-tables tr:nth-child(even){background-color: #fa8320;}

.large-tables tr:hover {background-color: #ddd;}

.large-tables th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #2048fa;
  color: white;
}
